.char-list {
    max-width: 650px;
    width: 100%;

    &__grid {
        width: 650px;
        margin: 0;
        padding: 0;
        list-style-type: none;
        display: grid;
        grid-template: 318px / repeat(3, 200px);
        grid-auto-rows: 318px;
        gap: 31px 25px;
        margin-bottom: 45px;
    }

    &__item {
        box-shadow: 5px 5px 20px rgba(0, 0, 0, 0.25);

        &:hover,
        &:focus,
        &__selected {
            outline: none;
            transform: translateY(-7px);
            transition: 0.5s all;
            box-shadow: 0px 5px 20px 0px rgba(159, 0, 19, 1);
        }
    }

    &__img {
        display: block;
        width: 200px;
        height: 200px;
    }

    &__name {
        background-color: #232222;
        height: 118px;
        color: #fff;
        text-transform: uppercase;
        font-style: 22px;
        font-weight: 700;
        padding: 15px;
    }

    .button {
        margin: 0 auto 45px auto;
    }
}