.comics-list {
    margin-top: 45px;
    margin-bottom: 50px;
    &__grid {
        list-style-type: none;
        padding: 0;
        margin: 0;
        display: grid;
        grid-template: auto / repeat(4, 225px);
        grid-auto-rows: auto;
        gap: 55px 67px;
    }

    &__img {
        width: 225px;
    }

    &__item {
        &__descr {
            margin-top: 10px;
            font-weight: 700;
            font-size: 14px;
        }

        &__price {
            margin-top: 5px;
            font-weight: 700;
            font-size: 14px;
            color: rgba(0, 0, 0, 0.6);
        }
    }

    .button{
        margin: 45px auto 0 auto;
    }
}