.random_char {
    min-height: 250px;
    width: 100%;
    display: flex;
    align-items: center;
    box-shadow: 5px 5px 20px rgba(0, 0, 0, 0.25);

    &__block {

        padding: 35px 40px;
        display: grid;
        grid-template: auto / 180px auto;
        column-gap: 25px;
        width: 50%;

        &__img {
            display: inline;
            width: 180px;
            height: 180px;
        }

        &__info {
            display: grid;
            grid-template-rows: 30px auto 38px;
            row-gap: 10px;
        }

        &__title {
            font-weight: 700;
            font-size: 22px;
            text-transform: uppercase;
        }

        &__descr {
            font-weight: 400;
            font-size: 14px;
        }

        &__btns {
            display: grid;
            grid-template-columns: repeat(2, min-content);
            column-gap: 30px;
        }
    }

    &__static {
        position: relative;
        background-color: #232222;
        padding: 35px 40px;
        width: 50%;
        :nth-child(2) {
            margin-top: 33px;
        }

        &__img {
            position: absolute;
            left: 397px;
            top: 47px;
        }

        &__title {
            font-weight: 700;
            font-size: 24px;
            color: #fff;
        }

        .button{
            margin-top: 20px;
        }
    }
}