@import './variables.scss';

.button {
    display: block;
    color: #fff;
    text-align: center;
    text-transform: uppercase;
    font-size: 14px;
    border: none;
    cursor: pointer;
    text-decoration: none;
    min-width: 101px;
    background: linear-gradient(135deg, transparent 10px, $main-color 0) left, linear-gradient(-45deg, transparent 10px, $main-color 0) right;
    background-size: 51% 100%;
    background-repeat: no-repeat;
    padding: 11px 0;
    width: min-content;
    transition: all 0.5s;

    .inner {
        padding: 0 18px;
        display: inline;
    }


    &__secondary {
        background: linear-gradient(135deg, transparent 10px, #5C5C5C 0) left, linear-gradient(-45deg, transparent 10px, #5C5C5C 0) right;
        background-size: 51% 100%;
        background-repeat: no-repeat;
    }

    &:hover {
        transform: translateY(-5px);
    }

    &__long {
        min-width: 170px;
    }

    &:disabled {
        filter: grayscale(.5);
    }
}