.char-info {
    width: 425px;
    padding: 25px;
    height: min-content;
    background-color: #fff;
    box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.25);

    &__header {
        display: grid;
        grid-template: 29px auto / 150px auto;
        gap: 35px 25px;

        .button__secondary {
            margin-top: 10px;
        }
    }

    &__title {
        font-weight: 700;
        font-size: 22px;
        text-transform: uppercase;
    }

    &__img {
        grid-row: 1 / 3;
        width: 150px;
        height: 150px;
    }

    &__descr {
        margin-top: 15px;
        font-weight: 400;
        font-size: 14px;
    }

    &__comics {
        margin-top: 10px;
        font-weight: 700;
        font-size: 18px;

        &-list {
            margin: 0;
            padding: 0;
            list-style-type: none;

            li {
                margin-top: 10px;
                padding: 4px 10px;
                box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
                font-weight: 400;
                font-size: 14px;
            }
        }
    }

    &__select {
        display: block;
        font-weight: bold;
        font-size: 18px;
        line-height: 24px;
        text-align: center;
    }
}