@import '../../style/variables.scss';

.header {
    margin: 50px auto;
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    font-weight: 700;

    a {
        cursor: pointer;
        text-decoration: none;
        color: $secondary-color;
    }

    &__title {
        font-size: 28px;

        span {
            color: $main-color;
        }
    }

    &__menu {
        font-size: 24px;

        ul {
            margin: 0;
            padding: 0;
            list-style: none;

            li {
                cursor: pointer;
                display: inline-block;

                &:hover a {
                    color: $main-color;
                }
            }

            .active a {
                color: $main-color;
            }
        }
    }
}